/************************/
/*  Features Styling    */
/************************/

@import "./Constants";

.features-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.feature-card{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 2em 5%;
}

.first-feature-card{
  margin-top: 0px;
}

.feature-text-left{
  width: 30%;
  max-width: 500px;
  padding: 2em;
  justify-self: right;
}
.feature-text-right{
  width: 30%;
  max-width: 500px;
  padding: 2em;
}

.feature-heading{
  // color: $dark;
  color: $lightLight;
  // max-width: 70%;
  // margin-left: 10%
}
.feature-paragraph{
  // color: $midDark;
  color: $midLight;
  // max-width: 70%;
  // margin-left:10%;
}

