/*******************
 Mobile Phone Queries
********************/



@media (max-width: 1000px) {

  .feature-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2em 5%;
  text-align: center;
}

.feature-text-left{
  width: 95%;
  max-width: 500px;
  padding: 2em;
  justify-self: right;
}
.feature-text-right{
  width: 95%;
  max-width: 500px;
  padding: 2em;
}


