//     font-size: 0.8em;

// }

// }


@media (max-width: 415px) {

    .grid-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 95%;

    margin-bottom: 40px;
    }

    .contributor {
    height: 70%;
    justify-self: center;
    }

    .individual-pic-container {
        width: 50%;
    }

    .flex-container {
        margin: 5px auto 25px auto;
    }
    #contributor-pictures p {
        margin: 5px;

    }

}


